@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Rasa:ital,wght@0,300..700;1,300..700&display=swap');


@import url('https://fonts.googleapis.com/css2?family=Cabin+Sketch:wght@400;700&family=REM:ital,wght@0,100..900;1,100..900&family=Rasa:ital,wght@0,300..700;1,300..700&family=Sigmar+One&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Cabin+Sketch:wght@400;700&family=REM:ital,wght@0,100..900;1,100..900&family=Rasa:ital,wght@0,300..700;1,300..700&family=Sigmar+One&family=Song+Myung&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image:url('./components/mountain4.jpg') ;
  background-repeat:no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-size: 100% 100%;
  /* box-shadow: 20px 20px 10px gray; */
  animation: slider 100s linear infinite;
}

/* .bg{
  background-size: 100% 100%;
  box-shadow: 20px 20px 10px gray;
  animation: slider 10s linear infinite;
} */

@keyframes slider{
  
  25%{background-image:url('./components/mountain1.jpg');}
  50%{background-image:url('./components/mountain2.jpg');}
  75%{background-image:url('./components/mountain3.jpg');}
  100%{background-image:url('./components/mountain4.jpg');}
}

.App {
  text-align: center;
}

h1 {
  color: #ffffff;
  margin-bottom: 20px;
  font-size: 40px;
  background-color: #103c39;
  border-radius: 15px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.43) ;
  padding:20px 20px;
  font-family: "Song Myung", serif;
}

.TodoWrapper {
  backdrop-filter: blur(10px);
  width: 100%;
  border-radius: 15px;
  padding: 32px;
  backdrop-filter: blur(50px);
}

.main{
  height: 100vh;
  width: 100%;
  margin-top: 200px;
  align-items: center;
}

.board{
  margin-right: 10px;
}

.does{
  background-color: #237c748c;
  border-radius: 15px;
  padding: 20px 20px;
  

}

.TodoForm {
  width: 100%;
  margin-bottom: 1rem;
}

.todo-input {
  outline: none;
  background-color: #103c39;
  border: 1px solid #5ea5a0;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  border-radius: 10px; color: #ffffff;
  font-size: 20px;
  font-family: "Song Myung", serif;
  color: white;
  
}

.todo-input::placeholder {
  color: #ffffff5d;
  
  
}

.todo-btn {
  background: #103c39;
  color: #ffffff;
  border: none;
  border-radius: 10px;
  padding: 10px;
  cursor: pointer;
  font-size: 20px;
  font-family: "Rasa", serif;
  margin: 10px;
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #103c39;
  color: #ffffff;
  padding: 0.75rem 1rem;
  border-radius: 15px;
  margin-bottom: 1rem;
}

.Todo p{
  cursor: pointer;
  font-family: "Song Myung", serif;
  font-size: 20px;
  width: 230px;
  overflow: auto ;
  white-space: nowrap;
}

.Todo p::-webkit-scrollbar{
  display: none;
}

.task{
  width: 70%;
  text-align: left;
}


.del {
  margin-left: 20px;
  
}

.incompleted {
  color: #fff;
  cursor: pointer;
}

.completed {
  color: #000000;
  text-decoration:line-through;
  cursor: pointer;
}

.check input{
  margin: 6px;
  cursor: pointer;
}


.delete-icon {
  cursor: pointer;
}